
import { defineComponent } from "vue";
import{throttle} from "../main"

export default defineComponent({
  props: {
    title: String,
    content: String,
    icon: String,
    image: String,
  },
  data(){
    return{
      copied:false
    }
  },
  methods: {
    hideModal: function (e: Event) {
      e.stopPropagation();

      throttle(()=>{

      const target = e.target as HTMLElement
      if(target.classList.contains("modal-card")){
          this.$emit("change-status");
      }else{
          this.copyContent();
      }
      },500)()
    
    },

    copyContent(){
     navigator.clipboard.writeText(this.content as string).then(()=>{
      this.copied=true;
      setTimeout(()=>{
        this.copied=false;
      },1000)
     })
    }
  },
});
