import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import 'bootstrap'
import {createI18n} from "vue-i18n"




const messages = {
    en: require('./locales/en.json'),
    zh: require('./locales/zh.json'),
  }
  
 const i18n = createI18n({
    locale:'en',
    fallbackLocal:'en',
    messages,
})

console.log(messages)

const app = createApp(App)
app.use(router)
app.use(i18n)
app.mount('#app')


let flag=true
export function throttle(fn: any, delay: number) {
  return function () {
    if (flag) {
      setTimeout(() => {
        fn();
        flag = true;
      }, delay);
    }
    flag = false;
  };
}

export function debounce(fn: any, delay: number) {
  let t: any = null;
  return function () {
    // 若已经开始执行了
    if (t != null) {
      clearTimeout(t);
    }
    // 重新开始
    t = setTimeout(() => {
      fn();
      flag = true;
    }, delay);
  };
}




// 以iphone8 的宽度作为基准
const rootFontSize = () => {
        const w = window.screen.width
        const n = 16 * (w / 375) > 32 ? "32px" : (16 * (w / 375) + "px")
        document.documentElement.style.fontSize = n
}

window.addEventListener("load", rootFontSize)
window.addEventListener("resize", rootFontSize)


