
<template>
  <div class="modal-card" @click="hideModal">
  
    <div class="modal-content px-4  row justify-content-center">

      <div class="title col-12 text-center pt-2 pt-md-0">
        {{ $t(title) }}
      </div>
      <div class="content col-12 text-center pb-md-1 pb-2 ">
        {{ content }}
      </div>
      <div v-if="image" class="col-12 text-center pb-2 pb-md-1">
             <img  :src="image" alt="" class="image  "/>
      </div>
  
      <div class="copied col-12  text-center" v-show="copied">{{$t("copied")}}<i class="ms-1 fas fa-check"></i></div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import{throttle} from "../main"

export default defineComponent({
  props: {
    title: String,
    content: String,
    icon: String,
    image: String,
  },
  data(){
    return{
      copied:false
    }
  },
  methods: {
    hideModal: function (e: Event) {
      e.stopPropagation();

      throttle(()=>{

      const target = e.target as HTMLElement
      if(target.classList.contains("modal-card")){
          this.$emit("change-status");
      }else{
          this.copyContent();
      }
      },500)()
    
    },

    copyContent(){
     navigator.clipboard.writeText(this.content as string).then(()=>{
      this.copied=true;
      setTimeout(()=>{
        this.copied=false;
      },1000)
     })
    }
  },
});
</script>



<style lang="scss" scoped>
.modal-card {
  z-index: 2000 !important;
  top: 0;
  left: 0;
  right: 0;
  bottom:0;
  position: fixed;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.52);

}



.modal-content {
  position: relative;
  opacity: 0;
  width: 280px;
  height: fit-content;
  border-radius: 8px;
  border: none;
  padding: 1rem 0 1.2rem 0;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  animation: fadein 0.2s ease-in-out 0.2s forwards;

  & .image{
    width:180px;
  }

  &:hover{
    cursor: pointer;
  }


 
}
.copied{
  position: absolute;

  font-weight:600;
  left: calc(50% - 60px);
  font-size: 10px;
  width: 120px;
  color: white;
  line-height: 20px;
  top: calc(100% - 1.1rem);
  animation: moveup 0.2s ease;
  border-radius: 8px;
  background:  #1c56ad;
  // border: 3px solid  #1c56ad; 
}


@media (max-width:768px) {
  .copied{
      top: calc(100% - 1.6rem);
  }
  
}

@keyframes moveup {
  0%{
    opacity: 0;

  }
  // 100%{
  //    opacity: 1;
  //   top: calc(100% - 1.2rem);
  // }
  50%{
    opacity: 0.5;
    transform: scaleX(1.1);
  }
  
}

@media (min-width: 768px) {
  .modal-content {
        width: 400px;
  }
}

@keyframes fadein {
  0%{
        transform: translateY(-5%);
  }

  100% {
    opacity: 1;
  }
}

@keyframes popup {
  50%{
      opacity: 0.5;
       transform: scaleX(0.9);
  }

  100% {
    opacity: 1;
  }
}

.title {
  font-size: 16px;
  font-weight: bold;
}

.content {
  font-size: 14px;
  font-weight: normal;
}
</style>